export class SupportAgent {
  constructor() {
    this.name = 'Customer Support Agent';
    this.version = '1.0.0';
    this.capabilities = ['response_generation', 'sentiment_analysis', 'escalation_detection'];
    this.isActive = false;
    this.processedTickets = 0;
    this.resolvedTickets = 0;
    
    // Response templates by category
    this.responseTemplates = {
      'Technical': {
        greeting: 'Thank you for reaching out about this technical issue.',
        investigation: 'I\'ve investigated your account and identified the following:',
        resolution: 'Here\'s how we can resolve this:',
        followUp: 'Please let me know if this resolves your issue or if you need further assistance.'
      },
      'Billing': {
        greeting: 'Thank you for contacting us about your billing inquiry.',
        investigation: 'I\'ve reviewed your account and billing history:',
        resolution: 'I\'ve taken the following actions to resolve this:',
        followUp: 'The changes should appear in your account within 3-5 business days. Please contact us if you have any questions.'
      },
      'Feature Request': {
        greeting: 'Thank you for your valuable feedback and feature suggestion.',
        investigation: 'I\'ve reviewed your request for:',
        resolution: 'Here\'s the current status and what you can expect:',
        followUp: 'I\'ll keep you updated on any developments regarding this feature.'
      }
    };
  }

  /**
   * Start the agent
   */
  start() {
    this.isActive = true;
    console.log('Support Agent started');
  }

  /**
   * Stop the agent
   */
  stop() {
    this.isActive = false;
    console.log('Support Agent stopped');
  }

  /**
   * Get agent statistics
   * @returns {Object} Agent stats
   */
  getStats() {
    return {
      active: this.isActive,
      processed: this.processedTickets,
      resolved: this.resolvedTickets,
      avgTime: '2.4h',
      satisfaction: 4.2
    };
  }

  async draftSupportReply(ticketData) {
    // Increment processed counter
    this.processedTickets++;
    
    // Simulate AI processing time
    await this.simulateProcessing(1500);
    
    // Analyze ticket content
    const analysis = this.analyzeTicket(ticketData);
    
    // Generate response
    const response = this.generateResponse(ticketData, analysis);
    
    return {
      ticketId: ticketData.id,
      suggestedResponse: response,
      confidence: analysis.confidence,
      category: ticketData.category,
      sentiment: analysis.sentiment,
      urgency: analysis.urgency,
      escalationNeeded: analysis.escalationNeeded,
      nextSteps: analysis.nextSteps,
      tags: this.generateTags(ticketData, analysis)
    };
  }

  analyzeTicket(ticketData) {
    const message = ticketData.message.toLowerCase();
    
    return {
      sentiment: this.analyzeSentiment(message),
      urgency: this.analyzeUrgency(ticketData),
      confidence: this.calculateConfidence(ticketData),
      escalationNeeded: this.shouldEscalate(ticketData, message),
      nextSteps: this.generateNextSteps(ticketData),
      keyTopics: this.extractKeyTopics(message)
    };
  }

  analyzeSentiment(message) {
    // Simple sentiment analysis based on keywords
    const positiveWords = ['thank', 'please', 'appreciate', 'great', 'good', 'excellent'];
    const negativeWords = ['urgent', 'critical', 'broken', 'frustrated', 'angry', 'terrible', 'awful'];
    
    let positiveScore = 0;
    let negativeScore = 0;
    
    positiveWords.forEach(word => {
      if (message.includes(word)) positiveScore++;
    });
    
    negativeWords.forEach(word => {
      if (message.includes(word)) negativeScore++;
    });
    
    if (negativeScore > positiveScore) return 'negative';
    if (positiveScore > negativeScore) return 'positive';
    return 'neutral';
  }

  analyzeUrgency(ticketData) {
    // Determine urgency based on priority and content
    let urgency = ticketData.priority === 'High' ? 'high' : 'medium';
    
    const urgentKeywords = ['urgent', 'critical', 'emergency', 'immediately', 'asap'];
    const message = ticketData.message.toLowerCase();
    
    if (urgentKeywords.some(keyword => message.includes(keyword))) {
      urgency = 'high';
    }
    
    return urgency;
  }

  calculateConfidence(ticketData) {
    // Base confidence on category and content clarity
    let confidence = 0.7;
    
    if (ticketData.category === 'Technical') confidence += 0.1;
    if (ticketData.category === 'Billing') confidence += 0.15;
    if (ticketData.message.length > 100) confidence += 0.1;
    
    return Math.min(confidence, 0.95);
  }

  shouldEscalate(ticketData, message) {
    // Escalation logic
    if (ticketData.priority === 'High') return true;
    if (message.includes('urgent') || message.includes('critical')) return true;
    if (ticketData.category === 'Technical' && message.includes('down')) return true;
    
    return false;
  }

  generateNextSteps(ticketData) {
    const steps = [];
    
    if (ticketData.category === 'Technical') {
      steps.push('Verify system status');
      steps.push('Check user permissions');
      steps.push('Test reproduction steps');
    }
    
    if (ticketData.category === 'Billing') {
      steps.push('Review account history');
      steps.push('Verify payment method');
      steps.push('Check billing cycles');
    }
    
    steps.push('Follow up within 24 hours');
    
    return steps;
  }

  extractKeyTopics(message) {
    const topics = [];
    
    if (message.includes('login') || message.includes('password')) topics.push('authentication');
    if (message.includes('billing') || message.includes('payment')) topics.push('billing');
    if (message.includes('feature') || message.includes('function')) topics.push('feature');
    if (message.includes('bug') || message.includes('error')) topics.push('bug');
    
    return topics;
  }

  generateResponse(ticketData, analysis) {
    const template = this.responseTemplates[ticketData.category] || this.responseTemplates['Technical'];
    
    let response = `Dear ${ticketData.customerName},\n\n`;
    response += template.greeting + '\n\n';
    
    // Add category-specific content
    if (ticketData.category === 'Technical') {
      response += 'I understand you\'re experiencing technical difficulties. ';
      response += 'Our team is investigating this issue and will provide a solution shortly.\n\n';
      response += 'In the meantime, please try:\n';
      response += '1. Clearing your browser cache\n';
      response += '2. Trying an incognito/private browsing window\n';
      response += '3. Checking your internet connection\n\n';
    } else if (ticketData.category === 'Billing') {
      response += 'I\'ve reviewed your billing inquiry and am working to resolve this promptly. ';
      response += 'I\'ll investigate your account details and provide an update within 24 hours.\n\n';
    } else if (ticketData.category === 'Feature Request') {
      response += 'Thank you for this valuable suggestion! ';
      response += 'I\'ve forwarded your request to our product team for review. ';
      response += 'Feature requests are evaluated quarterly based on customer feedback and business impact.\n\n';
    }
    
    response += template.followUp + '\n\n';
    response += `Ticket ID: ${ticketData.id}\n`;
    response += `Priority: ${ticketData.priority}\n`;
    response += `Category: ${ticketData.category}\n\n`;
    response += 'Best regards,\nCRM Cloud Support Team';
    
    return response;
  }

  generateTags(ticketData, analysis) {
    const tags = [ticketData.category.toLowerCase().replace(' ', '_')];
    
    // Add priority tag
    tags.push(`priority_${ticketData.priority.toLowerCase()}`);
    
    // Add sentiment tag
    if (analysis.sentiment !== 'neutral') {
      tags.push(`sentiment_${analysis.sentiment}`);
    }
    
    // Add urgency tag
    if (analysis.urgency === 'high') {
      tags.push('urgent');
    }
    
    // Add escalation tag
    if (analysis.escalationNeeded) {
      tags.push('escalated');
    }
    
    // Add topic-based tags
    tags.push(...analysis.keyTopics);
    
    return tags;
  }

  async simulateProcessing(ms = 1500) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
}

// Export singleton instance
export const supportAgent = new SupportAgent();